.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0;
  }
  
  .v {
    font-size: 5rem;
    font-weight: bold;
    color: #4a90e2; 
    animation: fade 1s infinite;
  }
  
  @keyframes fade {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
  }
  